// 16px
.g-16,
.gx-16 {
	--bs-gutter-x: 1rem;
}
.g-16,
.gy-16 {
	--bs-gutter-y: 1rem;
}

// 20px
.g-20,
.gx-20 {
	--bs-gutter-x: 1.25rem;
}
.g-20,
.gy-20 {
	--bs-gutter-y: 1.25rem;
}

// 25px
.g-25,
.gx-25 {
	--bs-gutter-x: 1.55rem;
}
.g-25,
.gy-25 {
	--bs-gutter-y: 1.55rem;
}

// 30px
.g-30,
.gy-30 {
	--bs-gutter-y: 1.875rem;
}
.g-30,
.gx-30 {
	--bs-gutter-x: 1.875rem;
}

// 40px
.g-40,
.gy-40 {
	--bs-gutter-y: 40px;
}
.g-40,
.gx-40 {
	--bs-gutter-x: 40px;
}

// 60px
.g-60,
.gx-60 {
	--bs-gutter-x: 3.75rem;
}
.g-60,
.gy-60 {
	--bs-gutter-y: 3.75rem;
}

// 70px
.g-70,
.gx-70 {
	--bs-gutter-x: 70px;
}
.g-70,
.gy-70 {
	--bs-gutter-y: 70px;
}


.custom-col-5 {
	width: 20%;

	@include md {
		width: 50%;
	}

	@media (max-width: 440px) {
		width: 100%;
	}
}
